import React from "react";
import iconsSprite from "./../../assets/img/img-sprite.svg";

type IconPropsType = {
   iconId: string
   width?: string
   height?: string
   viewBox?: string
}

export const Icon = (props: IconPropsType) => {
   return (
      <svg width={props.width || "474"} height={props.height || "124"} viewBox={props.viewBox || "0 0 474 124"} fill="none" xmlns="http://www.w3.org/2000/svg">
         <use xlinkHref={`${iconsSprite}#${props.iconId}`}/>
      </svg>
   );
};