const StyledScreen = styled.g`
   animation: screen 2.5s ease-in-out 0s infinite forwards   ;
   @keyframes screen {
      0% {
         transform: translateY(12px);
      }
      50% {
         transform: translateY(0);
      }
      100% {
         transform: translateY(12px);
      }
   }
`

